:root {
	--talesoftheoldwest-c-dark: #191813;
	--talesoftheoldwest-c-faint: #c9c7b8;
	--talesoftheoldwest-c-beige: #b5b3a4;
	--talesoftheoldwest-c-tan: #444444;
	--talesoftheoldwest-c-white: #ffffff;
	--talesoftheoldwest-c-black: #000000;
	--talesoftheoldwest-c-groove: #eeede0;
}

.theme-dark {
	--talesoftheoldwest-c-dark: #191813;
	--talesoftheoldwest-c-faint: #c9c7b8;
	--talesoftheoldwest-c-beige: #b5b3a4;
	--talesoftheoldwest-c-tan: #444444;
	--talesoftheoldwest-c-white: #ffffff;
	--talesoftheoldwest-c-black: #000000;
	--talesoftheoldwest-c-groove: #eeede0;
}

$c-white: var(--talesoftheoldwest-c-white);
$c-black: var(--talesoftheoldwest-c-black);

$c-dark: var(--talesoftheoldwest-c-dark);
$c-faint: var(--talesoftheoldwest-c-faint);
$c-beige: var(--talesoftheoldwest-c-beige);
$c-tan: var(--talesoftheoldwest-c-tan);

$c-groove: var(--talesoftheoldwest-c-groove);
