// Section Header
.items-header {
	height: 28px;
	margin: 2px 0;
	padding: 0;
	align-items: center;
	background: var(--black);
	border: $border-groove;
	font-weight: bold;
	> * {
		font-size: 14px;
		text-align: center;
	}
	.item-name {
		font-weight: bold;
		padding-left: 5px;
		text-align: left;
		display: flex;
		// font-size: 16px;
	}
}

// -----------------------------------------
// Items Lists
// -----------------------------------------

.items-list {
	list-style: none;
	margin: 0;
	padding: 0;
	overflow-y: auto;
	scrollbar-width: thin;
	color: $c-tan;

	// Child lists
	.item-list {
		list-style: none;
		margin: 0;
		padding: 0;
	}

	// Item Name
	.item-name {
		flex: 2;
		margin: 0;
		overflow: hidden;
		font-size: 13px;
		text-align: left;
		align-items: center;
		display: flex;
		font-weight: bolder;
		h3,
		h4 {
			margin: 0;
			white-space: nowrap;
			overflow-x: hidden;
		}
	}
	// Item Name
	.item-stat {
		flex: 2;
		margin: 0;
		overflow: hidden;
		font-size: 19px;
		/* text-align: center; */
		/* align-items: center; */
		display: flex;
		h3,
		h4 {
			margin: 0;
			white-space: nowrap;
			overflow-x: hidden;
		}
	}

	// Control Buttons
	.item-controls {
		display: flex;
		flex: 0 0 100px;
		justify-content: flex-end;
		a {
			font-size: 12px;
			text-align: center;
			margin: 0 6px;
		}
	}

	// Individual Item
	.item {
		align-items: center;
		padding: 0 2px; // to align with the header border
		border-bottom: 1px solid $c-faint;
		&:last-child {
			border-bottom: none;
		}
		.item-name {
			color: $c-dark;
			.item-image {
				flex: 0 0 30px;
				height: 30px;
				background-size: 30px;
				border: none;
				margin-right: 5px;
			}
		}
	}

	.item-prop {
		text-align: center;
		border-left: 1px solid #c9c7b8;
		border-right: 1px solid #c9c7b8;
		font-size: 12px;
	}

	// Section Header
	.items-header {
		height: 28px;
		margin: 2px 0 var(--black);
		padding: 0;
		align-items: center;
		background: rgba(0, 0, 0, 0.05);
		border: $border-groove;
		font-weight: bold;
		> * {
			font-size: 12px;
			text-align: center;
		}
		.item-name {
			padding-left: 17px;
			text-align: left;
			// font-size: 16px;
		}
	}
}

// Example style for TOTOW (can be removed if not needed).
.item-formula {
	flex: 0 0 200px;
	padding: 0 8px;
}
