.item-form {
	font-family: $font-primary;
}

.sheet-header {
	flex: 0 auto;
	overflow: hidden;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	margin-bottom: 10px;

	.profile-img {
		flex: 0 0 100px;
		height: 100px;
		margin-right: 10px;
	}

	.header-fields {
		flex: 1;
	}

	h1.charname {
		height: 50px;
		padding: 0px;
		margin: 5px 0;
		border-bottom: 0;
		input {
			width: 100%;
			height: 100%;
			margin: 0;
		}
	}
}

.sheet-tabs {
	flex: 0;
}

.sheet-body,
.sheet-body .tab,
.sheet-body .tab .editor {
	height: 100%;
}

.tab.description {
	flex: 1;

	.editor {
		height: 100%;
	}
}

.tox {
	.tox-editor-container {
		background: $c-white;
	}

	.tox-edit-area {
		padding: 0 8px;
	}
}

.sidebar {
	width: 88px;
}

.statnum {
	width: 35px;
	text-align: center;
	font-size: larger;
}
.dicenum {
	width: 60px;
}
